

.home-page{
  margin-top: 10px;
}


.filters{
  display: flex;
  width: 90%;
  margin: auto;
  gap: 10px;
}

.banner-imgs{
  display: flex;
  width:100%;
  overflow: hidden;
  gap: 20px;
}

.banner-img{
  width: 100vw;
  height: 550px;
}

.reset-button{
  width:25%;
  display: flex;
  align-items: center;
  justify-content: center;
  
 
}

.reset{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0px;
}

.category-filter{
  width:50%;
  border: 2px solid black;
  border-radius: 12px;
  padding: 10px;

}

.price-filter{
  border: 2px solid black;
  border-radius: 12px;
  padding: 10px;

}

.categories{
  display: flex;
  flex-wrap: wrap;
}

.prices{
  display: flex;
  flex-wrap: wrap;

}

.products-details{
  width: 80%;
  margin: auto;
  padding: 10px;
  border: 2px solid black;
  border-radius: 12px;
  margin-bottom: 10px;
  background-color: wheat;
}
.products-details h1{
  
  margin-bottom: 100px;
}


.price-filter{
  width: 50%;
}
.btns{
  background: transparent !important;
  border: none;
  color: antiquewhite;
}

.btn-info:hover{
 color: gray;
}

.btn-danger{
  width:40%;
}

.card-body{
  background-color: black;
  border-radius: 0px 0px 12px 12px;
}
.home-page .card {
  width: 18rem;
  border-radius: 12px;
  background:transparent;
  transition: all 0.6s ease;
}

.card:hover{
  transform: translateY(-10%);
}


.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}


.home-page .card-img-top {
  height: 300px !important;
}
/* ==================
filters
============== */
.filters h4 {
  margin-top: 100px;
  color: black !important;
  border-bottom: 1px solid black;
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}
.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
}

.loadmore {
  color: green;
  font-weight: bold;
  font-size: 20px !important;
}


.homepage{
  margin-top: 170px;
}